import React, { Fragment, useEffect } from "react";
import {
  Button,
  Dialog,
  DialogContent,
  Box,
  ButtonProps,
} from "@material-ui/core";
import GoogleIcon from "../img/GoogleIcon.svg";
import FacebookIcon from "../img/FacebookIcon.svg";
import {
  signInWithGoogle,
  signInWithFacebook,
  checkIfAlreadyLoggedIn,
} from "../firebase/auth";
import User from "../types/User";
import { useSnackbar } from "notistack";

interface LoginButtonProps extends ButtonProps {
  onLogin: (userCredential: firebase.auth.UserCredential) => void;
  onError: (error: any) => void;
}

const GoogleLoginButton = ({
  onLogin,
  onError,
  ...buttonProps
}: LoginButtonProps) => (
  <Button
    {...buttonProps}
    variant="contained"
    style={{ width: "100%", fontWeight: 600, background: "white" }}
    onClick={() => {
      signInWithGoogle()
        .then((u) => {
          onLogin(u);
        })
        .catch((e) => {
          onError(e);
        });
    }}
  >
    <Box display="flex" alignItems="center" py={0.5}>
      <Box mr={1} display="flex" alignItems="center">
        <img src={GoogleIcon} alt="Google" style={{ height: "1em" }} />
      </Box>
      רוצה להתחבר עם Google
    </Box>
  </Button>
);

const FacebookLoginButton = ({
  onLogin,
  onError,
  ...buttonProps
}: LoginButtonProps) => (
  <Button
    {...buttonProps}
    variant="contained"
    style={{
      width: "100%",
      fontWeight: 600,
      background: "#4267b2",
      color: "white",
    }}
    onClick={() => signInWithFacebook().then(onLogin).catch(onError)}
  >
    <Box display="flex" alignItems="center" py={0.5}>
      <Box mr={1} display="flex" alignItems="center">
        <img src={FacebookIcon} alt="Facebook" style={{ height: "1.5em" }} />
      </Box>
      האמת, בא לי Facebook
    </Box>
  </Button>
);

interface LoginDialogProps {
  open: boolean;
  setOpen: (open: boolean) => void;
  setLoggedInUser: (loggedInUser: User) => void;
}

const ErrorSnackbarContent = () => (
  <Fragment>
    נתקלנו בשגיאה בנסיון ההתחברות.
    <br />
    נסו להתחבר בדרך אחרת, חכו קצת, או פנו אלינו לתמיכה ✨
  </Fragment>
);

export default function LoginDialog({
  open,
  setOpen,
  setLoggedInUser,
}: LoginDialogProps) {
  const { enqueueSnackbar } = useSnackbar();

  const handleError = (error: any) => {
    console.error(error);
    enqueueSnackbar(<ErrorSnackbarContent />, {
      variant: "error",
    });
  };

  const onLogin = (user: firebase.User) => {
    const { displayName, email } = user;
    const loggedInUser = new User(displayName, email);

    enqueueSnackbar("ברוכים הבאות!", {
      variant: "success",
    });
    setLoggedInUser(loggedInUser);
    setOpen(false);
  };

  const handleUserCredential = (
    userCredential: firebase.auth.UserCredential
  ) => {
    if (userCredential.user === null) {
      enqueueSnackbar(<ErrorSnackbarContent />, {
        variant: "error",
      });
      return;
    }
    onLogin(userCredential.user);
  };

  useEffect(() => {
    // We can't use `onLogin` as it's re-created every render
    checkIfAlreadyLoggedIn().then((user: firebase.User) => {
      const { displayName, email } = user;
      const loggedInUser = new User(displayName, email);

      setLoggedInUser(loggedInUser);
      setOpen(false);
    });
  }, [setLoggedInUser, setOpen]);

  return (
    <Fragment>
      <Dialog
        open={open}
        onClose={() => setOpen(false)}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogContent style={{ padding: "0", backgroundColor: "#dcdcdc" }}>
          <Box p={3}>
            <Box display="flex" justifyContent="stretch">
              <GoogleLoginButton
                onLogin={handleUserCredential}
                onError={handleError}
              />
            </Box>
            <Box mt={2} display="flex" justifyContent="stretch">
              <FacebookLoginButton
                onLogin={handleUserCredential}
                onError={handleError}
              />
            </Box>
          </Box>
        </DialogContent>
      </Dialog>
    </Fragment>
  );
}
