import React, {
  useState,
  Fragment,
  useReducer,
  useContext,
  useEffect,
} from "react";
import {
  Typography,
  makeStyles,
  Box,
  Card,
  CardContent,
  TextField,
  InputAdornment,
  Button,
} from "@material-ui/core";
import HomeSearchIcon from "../icons/HomeSearchIcon";
import SearchFilter, { searchFilters } from "../searchFilters";
import server from "../server";
import { ServerBusiness } from "../types/Business";
import { PrimaryButton, ContainedPrimaryButton } from "./StyledButtons";
import BusinessesContainer from "./BusinessesContainer";
import SearchTitle from "../img/MultiGenderHebrew/SearchTitle";
import FocusedBusinessDialog from "./FocusedBusinessDialog";
import SearchEmptyState from "./SearchEmptyState";
import ProposeActionsContext from "../context/ProposeActionsContext";
import clsx from "clsx";
import { isBlankOrEmpty } from "../utils/String";
import { useSnackbar } from "notistack";

const useStyles = makeStyles((theme) => ({
  searchHeadlineContainer: {
    textAlign: "center",
    margin: "0rem auto",
    marginTop: ".85rem",
    "& > img": {
      height: "1.35rem",
      marginLeft: ".75em",
      marginBottom: ".25em",
      "&:first-of-type": {
        marginLeft: 0,
      },
    },
  },
  fieldsContainer: {
    display: "grid",
    gridGap: theme.spacing(1),
    gridTemplateColumns: "repeat(2, 1fr)",
    [theme.breakpoints.down("xs")]: {
      gridTemplateColumns: "1fr",
    },
  },
  freeTextField: {
    gridRow: 1,
    gridColumn: "1 / 3",
    [theme.breakpoints.down("xs")]: {
      gridColumn: 1,
    },
  },
  searchOption: {
    transition: "opacity .2s ease",
  },
  deactivatedSearchOption: {
    opacity: 0.35,
  },
}));

type SelectedFilterApplicatorsState = Map<string, string[]>;
type SelectedFilterApplicatorsReducerAction = {
  type: "change";
  filterKey: string;
  selectedOptions: string[];
};

function selectedFilterApplicatorsReducer(
  state: SelectedFilterApplicatorsState,
  action: SelectedFilterApplicatorsReducerAction
): SelectedFilterApplicatorsState {
  switch (action.type) {
    case "change":
      const newState = new Map(state);
      if (action.selectedOptions.length === 0) {
        newState.delete(action.filterKey);
      } else {
        newState.set(action.filterKey, action.selectedOptions);
      }
      return newState;
  }
}

enum SearchOption {
  FREE_TEXT,
  FILTERS,
}

function SearchContainer() {
  const classes = useStyles();

  const openProposeScreen = useContext(ProposeActionsContext)
    ?.openProposeScreen;

  const [
    selectedSearchOption,
    setSelectedSearchOption,
  ] = useState<SearchOption | null>(null);

  const [freeTextQuery, setFreeTextQuery] = useState("");
  const [selectedFilters, dispatchSelectedFiltersAction] = useReducer(
    selectedFilterApplicatorsReducer,
    new Map<string, string[]>()
  );
  const [results, setResults] = useState<ServerBusiness[] | null>(null);

  const [
    isFocusedBusinessDialogOpen,
    setIsFocusedBusinessDialogOpen,
  ] = useState(false);
  const [focusedBusiness, setFocusedBusiness] = useState<ServerBusiness | null>(
    null
  );

  const { enqueueSnackbar } = useSnackbar();

  const fetchResults = () => {
    if (
      selectedSearchOption === null ||
      (selectedSearchOption === SearchOption.FREE_TEXT &&
        isBlankOrEmpty(freeTextQuery)) ||
      (selectedSearchOption === SearchOption.FILTERS &&
        selectedFilters.size === 0)
    ) {
      enqueueSnackbar("צריך לבחור מסננים לחיפוש", { preventDuplicate: true });
      return;
    }

    const searchQuery = {
      freeText:
        selectedSearchOption === SearchOption.FREE_TEXT ? freeTextQuery : "",
      attributes:
        selectedSearchOption === SearchOption.FILTERS
          ? Object.fromEntries(selectedFilters)
          : {},
    };

    server.fetchBusinesses(searchQuery).then(setResults).catch(console.error);
  };

  useEffect(() => {
    if (!isBlankOrEmpty(freeTextQuery)) {
      setSelectedSearchOption(SearchOption.FREE_TEXT);
    }
  }, [freeTextQuery]);

  return (
    <Fragment>
      <Card>
        <CardContent style={{ padding: 0 }}>
          <Box p={2}>
            <Box mb={2}>
              <Box className={classes.searchHeadlineContainer}>
                <SearchTitle />
              </Box>
              <Typography align="center" color="textSecondary" gutterBottom>
                חפשו לפי שם נותן.ת השירות <strong>או</strong> סננו לפי השדות
              </Typography>
            </Box>
            <Box className={classes.fieldsContainer}>
              <TextField
                className={clsx(classes.freeTextField, classes.searchOption, {
                  [classes.deactivatedSearchOption]: !(
                    selectedSearchOption === null ||
                    selectedSearchOption === SearchOption.FREE_TEXT
                  ),
                })}
                label="שם נותן.ת השירות"
                value={freeTextQuery}
                onChange={(event) => setFreeTextQuery(event.target.value)}
                color="secondary"
                variant="filled"
                onFocus={() => setSelectedSearchOption(SearchOption.FREE_TEXT)}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <HomeSearchIcon />
                    </InputAdornment>
                  ),
                }}
              />
              {searchFilters.map((searchFilter: SearchFilter) => (
                <searchFilter.render
                  key={searchFilter.filterKey}
                  value={selectedFilters.get(searchFilter.filterKey) ?? []}
                  formControlProps={{
                    className: clsx(classes.searchOption, {
                      [classes.deactivatedSearchOption]: !(
                        selectedSearchOption === null ||
                        selectedSearchOption === SearchOption.FILTERS
                      ),
                    }),
                  }}
                  onChange={(selectedOptions) =>
                    dispatchSelectedFiltersAction({
                      type: "change",
                      filterKey: searchFilter.filterKey,
                      selectedOptions,
                    })
                  }
                  selectProps={{
                    onFocus: () => {
                      setSelectedSearchOption(SearchOption.FILTERS);
                    },
                  }}
                />
              ))}
            </Box>
            <Box display="flex" justifyContent="flex-end" mt={1}>
              <Button
                variant="contained"
                color="secondary"
                onClick={fetchResults}
              >
                חיפוש
              </Button>
            </Box>
          </Box>
        </CardContent>
      </Card>
      {results !== null ? (
        results.length === 0 ? (
          <SearchEmptyState />
        ) : (
          <BusinessesContainer
            businesses={results}
            rating="number"
            buttons={(business) => (
              <Fragment>
                <PrimaryButton
                  size="small"
                  style={{}}
                  onClick={() => {
                    setFocusedBusiness(business);
                    setIsFocusedBusinessDialogOpen(true);
                  }}
                >
                  פרטים נוספים
                </PrimaryButton>
                <ContainedPrimaryButton
                  size="small"
                  onClick={() => openProposeScreen?.(business)}
                >
                  יש לי מה להוסיף!
                </ContainedPrimaryButton>
              </Fragment>
            )}
          />
        )
      ) : (
        <Box mt={2} />
      )}
      <FocusedBusinessDialog
        open={isFocusedBusinessDialogOpen}
        onClose={() => setIsFocusedBusinessDialogOpen(false)}
        focusedBusiness={focusedBusiness}
        openProposeScreen={openProposeScreen}
      />
    </Fragment>
  );
}

export default SearchContainer;
