export const formatDateToDisplay = (date: Date) => {
  return date.toLocaleDateString("he", {
    day: "numeric",
    month: "numeric",
    year: "2-digit",
  });
};

const SECONDS_IN_MINUTE = 60;
const SECONDS_IN_HOUR = 3600;
const SECONDS_IN_DAY = 86400;
const SECONDS_IN_MONTH = 2592000;
const SECONDS_IN_YAER = 31536000;

export function timeSinceInHebrew(date: Date): string {
  let seconds = Math.floor((new Date().getTime() - date.getTime()) / 1000);

  let interval = seconds / SECONDS_IN_YAER;

  if (interval > 1) {
    return `לפני ${Math.floor(interval)} שנים`;
  }
  interval = seconds / SECONDS_IN_MONTH;
  if (interval > 1) {
    return `לפני ${Math.floor(interval)} חודשים`;
  }
  interval = seconds / SECONDS_IN_DAY;
  if (interval > 1) {
    return `לפני ${Math.floor(interval)} ימים`;
  }
  interval = seconds / SECONDS_IN_HOUR;
  if (interval > 1) {
    return `לפני ${Math.floor(interval)} שעות`;
  }
  interval = seconds / SECONDS_IN_MINUTE;
  if (interval > 1) {
    return `לפני ${Math.floor(interval)} דקות`;
  }

  if (seconds > 1) {
    return `לפני ${Math.floor(seconds)} שניות`;
  }

  return "ברגע זה";
}
