import React, { Fragment, useState } from "react";
import {
  Box,
  Typography,
  Button,
  FormControlLabel,
  Checkbox,
} from "@material-ui/core";
import { ContainedPrimaryButton } from "../../components/StyledButtons";
import BusinessView from "../../components/BusinessView";
import {
  Proposal,
  BusinessProposal,
  BusinessProposalType,
} from "../../types/Proposal";
import User from "../../types/User";
import { withAuthenticated } from "../../hocs/requiredAuthenticated";

interface Step4Props {
  businessResult: BusinessProposal;
  rating: number;
  moreDetails: string;
  handleBack: () => void;
  handleNext: () => void;
  onResult: (proposal: Proposal) => void;
  loggedInUser: User;
}

const Step4 = ({
  businessResult,
  rating,
  moreDetails,
  handleBack,
  onResult,
  loggedInUser,
}: Step4Props) => {
  const [isAnonymous, setIsAnonymous] = useState(false);

  const ratingText = (
    <Fragment>
      והדירוג שנתת הוא <b>{rating} כוכבים</b>
      {moreDetails ? (
        <Fragment>
          ,
          <br />
          בתוספת ההערה:
          <br /> {moreDetails}
        </Fragment>
      ) : (
        "."
      )}
    </Fragment>
  );

  let changeDescription: JSX.Element;
  switch (businessResult.type) {
    case BusinessProposalType.createNew:
      changeDescription = (
        <Fragment>
          <Typography>זה העסק שרצית להוסיף:</Typography>
          <BusinessView
            business={businessResult.business}
            rating={false}
            detailed
          />
        </Fragment>
      );
      break;
    case BusinessProposalType.useExisting:
      changeDescription = (
        <Fragment>
          <Typography>זה העסק שרצית לדרג:</Typography>
          <BusinessView
            business={businessResult.existingBusiness}
            rating={false}
            detailed
          />
        </Fragment>
      );
      break;
    case BusinessProposalType.alterExisting:
      changeDescription = (
        <Fragment>
          <Typography>
            בחרת לערוך את המידע על העסק{" "}
            <b>{businessResult.existingBusiness.name}</b>, לפרטים הבאים:
          </Typography>
          <BusinessView
            business={businessResult.business}
            rating={false}
            detailed
          />
        </Fragment>
      );
      break;
  }

  const send = () => {
    let result: Proposal = {
      author: loggedInUser,
      business: businessResult,
      isAnonymous,
      review: {
        rating,
        moreDetails,
      },
    };
    onResult(result);
  };

  return (
    <Fragment>
      <Box>
        <FormControlLabel
          control={
            <Checkbox
              checked={isAnonymous}
              onChange={(e) => setIsAnonymous(e.target.checked)}
            />
          }
          label="שליחה אנונימית"
        />
      </Box>
      <Typography>
        לפני שנשלח את המידע, בואו נעבור פעם אחרונה על הפרטים.
        <br />
        אחרי השליחה אנחנו נבחן את ההצעה שלך לשיפור המידע, ונכניס את הפרטים
        הנכונים.&nbsp;😇
      </Typography>
      {changeDescription}
      <Typography>{ratingText}</Typography>
      <Box display="flex" justifyContent="space-between" mt={3}>
        <Button variant="outlined" onClick={handleBack}>
          חזרה לדירוג
        </Button>

        <ContainedPrimaryButton variant="contained" onClick={send}>
          כן, הכל פה נכון, בואו נשלח!
        </ContainedPrimaryButton>
      </Box>
    </Fragment>
  );
};

export default withAuthenticated(Step4);
