import { ServerReview } from "./Review";
import { average } from "../utils/Array";
import { Business } from "@material-ui/icons";

export interface BusinessLocation {
  address: string;
}

export interface BusinessInfo {
  phoneNumbers: string[];
  locations: BusinessLocation[];

  area: string[];
  expertise: string[];
  gender: string[];
  healthCare: string[];
  language: string[];
  profession: string[];
}

interface Business {
  name: string;
  info: BusinessInfo;
}

export interface ServerBusiness extends Business {
  id: number;
  reviews?: ServerReview[];
}

export const averageRating = (business: ServerBusiness) =>
  (business?.reviews?.length ?? 0) > 0
    ? average(business.reviews!.map((review) => review.content.rating))
    : null;

export default Business;
