import React, { useCallback, useState } from "react";
import {
  CssBaseline,
  Collapse,
  useTheme,
  Fade,
  Container,
} from "@material-ui/core";
import MainAppBar from "./components/MainAppBar";
import Hero from "./components/Hero";
import SearchContainer from "./components/SearchContainer";
import LoginDialog from "./dialogs/LoginDialog";
import User from "./types/User";
import AdminDialogPanel from "./admin";
import ProposeScreen from "./dialogs/Propose";
import LoggedInUserContext from "./context/LoggedInUserContext";
import { ServerBusiness } from "./types/Business";
import ProposeActionsContext from "./context/ProposeActionsContext";

function App() {
  const theme = useTheme();
  const [loggedInUser, setLoggedInUser] = useState<User | null>(null);
  const [isLoginDialogOpen, setIsLoginDialogOpen] = useState(false);
  const [isProposeScreenOpen, setIsProposeScreenOpen] = useState(false);
  const [isAdminDialogOpen, setIsAdminDialogOpen] = useState(false);

  const [
    proposeScreenSelectedBusiness,
    setProposeScreenSelectedBusiness,
  ] = useState<ServerBusiness | null>(null);
  const openProposeScreen = useCallback(
    (business?: ServerBusiness) => {
      if (loggedInUser) {
        setProposeScreenSelectedBusiness(business || null);
        setIsProposeScreenOpen(true);
      } else {
        setIsLoginDialogOpen(true);
      }
    },
    [loggedInUser]
  );

  return (
    <React.Fragment>
      <CssBaseline />
      <LoggedInUserContext.Provider value={loggedInUser}>
        <MainAppBar
          setIsAdminDialogOpen={setIsAdminDialogOpen}
          setIsLoginDialogOpen={setIsLoginDialogOpen}
          setLoggedInUser={setLoggedInUser}
          isProposeScreenOpen={isProposeScreenOpen}
          setIsProposeScreenOpen={setIsProposeScreenOpen}
        />
        <ProposeActionsContext.Provider value={{ openProposeScreen }}>
          <main>
            <Collapse
              in={!isProposeScreenOpen}
              timeout={300}
              style={{
                transitionDelay: "0s",
              }}
            >
              <Hero onLoginButtonClick={() => setIsLoginDialogOpen(true)} />
            </Collapse>

            {!isProposeScreenOpen && (
              <Fade
                in={!isProposeScreenOpen}
                timeout={{
                  enter: 300,
                  exit: !isProposeScreenOpen ? 1 : 290,
                }}
                style={{
                  transitionDelay: isProposeScreenOpen ? "200ms" : "0s",
                }}
              >
                <Container
                  maxWidth="md"
                  style={{
                    position: "relative",
                    marginTop: `-${theme.spacing(4)}px`,
                  }}
                >
                  <SearchContainer />
                </Container>
              </Fade>
            )}

            {isProposeScreenOpen && (
              <Fade
                in={isProposeScreenOpen}
                timeout={{
                  enter: 300,
                  exit: isProposeScreenOpen ? 1 : 290,
                }}
                style={{
                  transitionDelay: isProposeScreenOpen ? "200ms" : "0s",
                }}
              >
                <Container
                  maxWidth="sm"
                  style={{
                    position: "relative",
                    marginTop: `${theme.spacing(3)}px`,
                  }}
                >
                  <ProposeScreen
                    selectedBusiness={proposeScreenSelectedBusiness}
                  />
                </Container>
              </Fade>
            )}
          </main>
        </ProposeActionsContext.Provider>

        <LoginDialog
          open={isLoginDialogOpen}
          setOpen={setIsLoginDialogOpen}
          setLoggedInUser={setLoggedInUser}
        />
        <AdminDialogPanel
          open={isAdminDialogOpen}
          onClose={() => setIsAdminDialogOpen(false)}
        />
      </LoggedInUserContext.Provider>
    </React.Fragment>
  );
}

export default App;
