import React, { Fragment } from "react";
import {
  Box,
  Button,
  Dialog,
  Divider,
  List,
  ListItem,
  makeStyles,
  Typography,
} from "@material-ui/core";
import { ServerBusiness } from "../types/Business";
import BusinessView from "./BusinessView";
import { AddTwoTone } from "@material-ui/icons";
import Rating from "./Rating";
import { timeSinceInHebrew } from "../utils/Date";

type FocusedBusinessDialogProps = {
  open: boolean;
  onClose: () => void;
  focusedBusiness: ServerBusiness | null;
  openProposeScreen?:
    | ((business?: ServerBusiness | undefined) => void)
    | undefined;
};

const useStyles = makeStyles((theme) => ({
  reviews: {
    marginTop: theme.spacing(1),
  },
  review: {
    padding: theme.spacing(1.5, 2),
    flexDirection: "column",
    alignItems: "stretch",
  },
  rating: {
    fontSize: "1.1em",
  },
  createdAt: {
    fontWeight: "bold",
    display: "inline",
  },
}));

const FocusedBusinessDialog = ({
  open,
  onClose,
  focusedBusiness,
  openProposeScreen,
}: FocusedBusinessDialogProps) => {
  const classes = useStyles();

  return (
    <Dialog open={open} onClose={onClose} maxWidth="sm" fullWidth>
      <Box p={1}>
        {focusedBusiness && (
          <Fragment>
            <BusinessView
              business={focusedBusiness}
              rating="stars"
              detailed
              buttons={
                <Fragment>
                  <Button
                    variant="text"
                    startIcon={<AddTwoTone />}
                    onClick={() => openProposeScreen?.(focusedBusiness)} // TODO
                    color="primary"
                  >
                    יש לי מה להוסיף
                  </Button>
                </Fragment>
              }
            />
            {focusedBusiness.reviews && (
              <List disablePadding className={classes.reviews}>
                {focusedBusiness.reviews.map((review, reviewIndex) => (
                  <Fragment key={reviewIndex}>
                    <Divider variant="middle" component="li" />
                    <ListItem className={classes.review}>
                      <Box
                        display="flex"
                        justifyContent="space-between"
                        alignItems="center"
                      >
                        <Typography>
                          {review.author
                            ? review.author.name
                            : "שולח.ת אנונימי.ת"}
                        </Typography>
                        <Rating
                          className={classes.rating}
                          value={review.content.rating}
                          readOnly
                        />
                      </Box>
                      {review.content.moreDetails && (
                        <Typography variant="body1">
                          {review.content.moreDetails}
                        </Typography>
                      )}
                      <Typography variant="caption" color="textSecondary">
                        {timeSinceInHebrew(review.createdAt)}
                      </Typography>
                    </ListItem>
                  </Fragment>
                ))}
              </List>
            )}
          </Fragment>
        )}
      </Box>
    </Dialog>
  );
};

export default FocusedBusinessDialog;
