import React, { Fragment } from "react";
import { Typography, makeStyles, Box, Badge } from "@material-ui/core";
import {
  PlaceTwoTone,
  LabelTwoTone,
  CallTwoTone,
  TranslateTwoTone,
  SpaTwoTone,
  BookTwoTone,
  MapTwoTone,
  FiberManualRecordTwoTone,
} from "@material-ui/icons";
import Rating from "./Rating";
import Business, { ServerBusiness, averageRating } from "../types/Business";
import { formatForDisplaying } from "../utils/Array";

const useStyles = makeStyles((theme) => ({
  container: {
    fontSize: theme.typography.body1.fontSize,
  },
  name: {
    flexGrow: 1,
    marginRight: theme.spacing(2),
    lineHeight: "1em",
  },
  ratingStars: {
    fontSize: "1.1em",
  },
  ratingNumber: {
    fontSize: "1.1rem",
    lineHeight: "1em",
    "& > .MuiBadge-badge": {
      transform: "unset",
      position: "unset",
    },
  },
  tinyText: {
    fontSize: ".8em",
  },
  details: {
    display: "grid",
    gridTemplateColumns: "1.1em 1fr",
    gridGap: ".25em .5em",
    "& .MuiSvgIcon-root": {
      fontSize: "1.1em",
      height: "1.1em",
      width: "1.1em",
    },
  },
  buttonsContainer: {
    display: "flex",
  },
  buttonsContainerSpaceBetweenButtons: {
    justifyContent: "center",
  },
  buttonsContainerFullWidthButtons: {
    [theme.breakpoints.up("xs")]: {
      borderTop: "1px solid #f7ecf9",
      "& .MuiButton-root": {
        borderRadius: 0,
        flexGrow: 1,
      },
    },
  },
}));

export type RatingProp = "number" | "stars";

type BusinessViewProps = {
  buttons?: JSX.Element;
  fullWidthButtons?: boolean;
  detailed?: boolean;
} & (
  | {
      business: Business;
      rating: false;
    }
  | {
      business: ServerBusiness;
      rating?: RatingProp;
    }
);

const BusinessView = ({
  business,
  buttons,
  fullWidthButtons = false,
  detailed = false,
  rating,
}: BusinessViewProps) => {
  const classes = useStyles();

  const ratingValue =
    rating !== null && averageRating(business as ServerBusiness);

  return (
    <Fragment>
      <Box className={classes.container} p={2} pb={1}>
        <Box display="flex" alignItems="center" flexWrap="wrap" mb={1}>
          <Typography variant="h6" className={classes.name}>
            {business.name}
          </Typography>
          {rating === "stars" && (
            <Rating
              className={classes.ratingStars}
              value={ratingValue as number}
              readOnly
            />
          )}
          {rating === "number" && ratingValue && (
            <Badge
              badgeContent={ratingValue}
              color="secondary"
              className={classes.ratingNumber}
            />
          )}
        </Box>

        <Box className={classes.details}>
          <LabelTwoTone />
          <Box title="מקצוע">
            {formatForDisplaying(business.info.profession)}
          </Box>

          {detailed && (
            <Fragment>
              <BookTwoTone />
              <Box title="התמחות">
                {formatForDisplaying(business.info.expertise)}
              </Box>
            </Fragment>
          )}

          <MapTwoTone />
          <Box title="איזור">{formatForDisplaying(business.info.area)}</Box>

          {business.info.locations && (
            <Fragment>
              <PlaceTwoTone />
              <Box title="כתובת">
                {business.info.locations.map((location, index) => (
                  <Fragment key={location.address}>
                    {0 < index && <br />}
                    {location.address}
                  </Fragment>
                ))}
              </Box>
            </Fragment>
          )}

          {detailed && (
            <Fragment>
              <CallTwoTone />
              <Box title="מספר טלפון">
                {business.info.phoneNumbers.map((phoneNumber, index) => (
                  <Fragment key={phoneNumber}>
                    {0 < index && <br />}
                    {phoneNumber}
                  </Fragment>
                ))}
              </Box>
            </Fragment>
          )}

          <TranslateTwoTone />
          <Box title="שפה">{formatForDisplaying(business.info.language)}</Box>

          <SpaTwoTone />
          <Box title="קופת חולים">
            {formatForDisplaying(business.info.healthCare)}
          </Box>

          <Fragment>
            <FiberManualRecordTwoTone />
            <Box title="מגדר">{formatForDisplaying(business.info.gender)}</Box>
          </Fragment>
        </Box>
      </Box>
      {buttons && (
        <Box
          className={
            classes.buttonsContainer +
            ` ${
              fullWidthButtons
                ? classes.buttonsContainerFullWidthButtons
                : classes.buttonsContainerSpaceBetweenButtons
            }`
          }
        >
          {buttons}
        </Box>
      )}
    </Fragment>
  );
};

export default BusinessView;
