import React, { useState, useEffect, useCallback } from "react";
import {
  Container,
  Box,
  makeStyles,
  Paper,
  Grid,
  Typography,
  Button,
  Snackbar,
} from "@material-ui/core";
import CircuitBoardPattern from "../img/CircuitBoard.svg";
import server from "../server";
import { BusinessProposalType, StoredProposal } from "../types/Proposal";
import BusinessView from "../components/BusinessView";
import { Alert } from "@material-ui/lab";
import { formatDateToDisplay } from "../utils/Date";

const GRADIENT =
  "rgba(255,255,255,1) 0%, rgba(245,172,202,.3) 50%, rgba(107,175,225,.3) 100%";

const useStyles = makeStyles((theme) => ({
  container: {
    backgroundImage: `url(${CircuitBoardPattern})`,
  },
  content: {
    position: "relative",
    background: "white",
    minHeight: "100vh",
    [theme.breakpoints.up("md")]: {
      "&::before, &::after": {
        content: "''",
        position: "absolute",
        height: "100%",
        width: `calc(50vw - ${theme.breakpoints.values.md / 2}px)`,
        top: 0,
      },
      "&::before": {
        background: `linear-gradient(90deg, ${GRADIENT})`,
        right: "100%",
      },
      "&::after": {
        background: `linear-gradient(270deg, ${GRADIENT})`,
        left: "100%",
      },
    },
  },
  appBarMargin: theme.mixins.toolbar,
  proposal: {
    marginTop: theme.spacing(1),
    padding: theme.spacing(2),
  },
}));

const AdminPanel = () => {
  const classes = useStyles();

  const [isErrorSnackBarOpened, setIsErrorSnackBarOpened] = useState(false);
  const [isSuccessSnackBarOpened, setIsSuccessSnackBarOpened] = useState(false);

  const [openProposals, setOpenProposals] = useState<StoredProposal[] | null>();

  const refreshProposals = useCallback(() => {
    server.getAllOpenProposals().then(setOpenProposals);
  }, []);

  useEffect(refreshProposals, []);

  const accept = (proposalId: number) => {
    server
      .acceptProposal(proposalId)
      .then(() => {
        setIsSuccessSnackBarOpened(true);
      })
      .catch(() => {
        setIsErrorSnackBarOpened(true);
      })
      .finally(() => refreshProposals());
  };
  const decline = (proposalId: number) => {
    server
      .declineProposal(proposalId)
      .then(() => {
        setIsSuccessSnackBarOpened(true);
      })
      .catch(() => {
        setIsErrorSnackBarOpened(true);
      })
      .finally(() => refreshProposals());
  };

  return (
    <Container className={classes.container}>
      <Container maxWidth="md" className={classes.content}>
        <div className={classes.appBarMargin}></div>
        <Box pt={2}>
          {openProposals &&
            openProposals.map((proposal, index) => (
              <Paper key={index} className={classes.proposal}>
                <Typography variant="body1">
                  <b>
                    {proposal.isAnonymous
                      ? "משתמש.ת אנונימי.ת"
                      : `${proposal.author!.name} (${proposal.author!.email})`}
                  </b>{" "}
                  הציע.ה
                  {proposal.createdAt
                    ? ` ב-${formatDateToDisplay(proposal.createdAt)}`
                    : ""}
                  :
                </Typography>
                {proposal.business.type ===
                BusinessProposalType.alterExisting ? (
                  <Grid container>
                    <Grid item xs={12} sm={6}>
                      <Typography variant="body1">
                        לערוך את פרטי העסק הזה:
                      </Typography>
                      <BusinessView
                        detailed
                        business={proposal.business.existingBusiness}
                        rating={false}
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <Typography variant="body1">לפרטים הבאים:</Typography>
                      <BusinessView
                        detailed
                        business={proposal.business.business}
                        rating={false}
                      />
                    </Grid>
                  </Grid>
                ) : proposal.business.type ===
                  BusinessProposalType.createNew ? (
                  <Box>
                    <Typography variant="body1">להוסיף את העסק:</Typography>
                    <BusinessView
                      detailed
                      business={proposal.business.business}
                      rating={false}
                    />
                  </Box>
                ) : null}
                <Box mt={1} display="flex" justifyContent="space-between">
                  <Button
                    variant="outlined"
                    onClick={() => decline(proposal.id)}
                  >
                    סירוב
                  </Button>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={() => accept(proposal.id)}
                  >
                    אישור
                  </Button>
                </Box>
              </Paper>
            ))}
        </Box>
      </Container>
      <Snackbar
        open={isSuccessSnackBarOpened}
        autoHideDuration={6000}
        onClose={() => setIsSuccessSnackBarOpened(false)}
      >
        <Alert onClose={() => setIsSuccessSnackBarOpened(false)}>
          הפעולה בוצעה בהצלחה
        </Alert>
      </Snackbar>
      <Snackbar
        open={isErrorSnackBarOpened}
        autoHideDuration={6000}
        onClose={() => setIsErrorSnackBarOpened(false)}
      >
        <Alert onClose={() => setIsSuccessSnackBarOpened(false)}>
          קרתה שגיאה, נסו שוב מאוחר יותר
        </Alert>
      </Snackbar>
    </Container>
  );
};

export default AdminPanel;
