import User from "./User";
import { ReviewContent } from "./Review";
import Business, { ServerBusiness } from "./Business";

export enum BusinessProposalType {
  useExisting = "useExisting",
  alterExisting = "alterExisting",
  createNew = "createNew",
}

export type BusinessProposal =
  | {
      type: BusinessProposalType.createNew;
      business: Business;
    }
  | {
      type: BusinessProposalType.useExisting;
      existingBusiness: ServerBusiness;
    }
  | {
      type: BusinessProposalType.alterExisting;
      existingBusiness: ServerBusiness;
      business: Business;
    };

export type Proposal = {
  author: User | null;
  isAnonymous: boolean;
  business: BusinessProposal;
  review: ReviewContent;
};

export type StoredProposal = Proposal & { id: number; createdAt?: any };
