import { Config } from "../utils/Config";
import { fetchAuthenticated } from "../firebase/auth";
import { ServerBusiness } from "../types/Business";
import { Proposal, StoredProposal } from "../types/Proposal";
import { ServerReview } from "../types/Review";

const apiBase = Config.debug
  ? "http://localhost:5000/"
  : "https://maavarim.herokuapp.com/";

const get = (path: string, params: any = {}, auth: boolean = true) => {
  const url = new URL(apiBase + path);
  Object.keys(params).forEach((key) =>
    url.searchParams.append(key, params[key])
  );

  return (auth ? fetchAuthenticated : fetch)(url.href, {
    method: "get",
  });
};

const post = (path: string, body: any = {}, auth: boolean = true) =>
  (auth ? fetchAuthenticated : fetch)(apiBase + path, {
    method: "post",
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
    },
    body: JSON.stringify(body),
  });

async function propose(proposal: Proposal): Promise<void> {
  return post("proposal/", proposal).then(({ status }) => {
    if (status !== 200) throw new Error();
  });
}

async function getAllOpenProposals(): Promise<StoredProposal[]> {
  const jsonRes = await (await get("proposal/")).json();
  return jsonRes["proposals"].map((proposalRaw: StoredProposal) => ({
    ...proposalRaw,
    createdAt:
      proposalRaw.createdAt && new Date(Date.parse(proposalRaw.createdAt)),
  })) as StoredProposal[];
}

async function acceptProposal(proposalId: number): Promise<void> {
  return post(`proposal/${proposalId}/accept/`).then(({ status }) => {
    if (status !== 200) throw new Error();
  });
}

async function declineProposal(proposalId: number): Promise<void> {
  return post(`proposal/${proposalId}/decline/`).then(({ status }) => {
    if (status !== 200) throw new Error();
  });
}

function parseReview(review: any): ServerReview {
  return {
    ...review,
    createdAt: review.createdAt && new Date(Date.parse(review.createdAt)),
  };
}

async function fetchBusinesses(query: any): Promise<ServerBusiness[]> {
  const jsonRes = await (
    await get(
      "business/",
      { ...query, attributes: JSON.stringify(query.attributes) },
      false
    )
  ).json();
  return jsonRes["businesses"].map((business: any) => ({
    ...business,
    reviews: business.reviews.map(parseReview),
  }));
}

const server = {
  propose,
  fetchBusinesses,
  getAllOpenProposals,
  acceptProposal,
  declineProposal,
};

export default server;
