import React from "react";
import {
  Container,
  AppBar as MuiAppBar,
  Toolbar,
  Typography,
  makeStyles,
  Box,
  IconButton,
  Fade,
} from "@material-ui/core";
import LogoJpeg from "../img/Logo.jpeg";
import { ChevronRightTwoTone } from "@material-ui/icons";

const useStyles = makeStyles((theme) => ({
  appBarTitle: {
    flexGrow: 1,
    [theme.breakpoints.down("xs")]: {
      display: "none",
    },
  },
  logoImage: {
    height: "3em",
  },
  spacer: {
    [theme.breakpoints.down("xs")]: {
      flexGrow: 1,
    },
  },
  profilePhoto: {
    height: "2em",
    borderRadius: "50%",
    marginInlineEnd: `${theme.spacing(1)}px`,
  },
}));

interface AppBarProps {
  title: string;
  buttons: JSX.Element;
  backButtonProps?: {
    onClick: () => void;
  };
  position?: "fixed" | "absolute" | "sticky" | "static" | "relative";
}

function AppBar({ title, buttons, backButtonProps, position }: AppBarProps) {
  const classes = useStyles();

  return (
    <MuiAppBar position={position ?? "static"}>
      <Toolbar>
        <Container maxWidth="md">
          <Box display="flex" alignItems="center">
            {backButtonProps && (
              <Fade in={backButtonProps !== undefined}>
                <IconButton
                  edge="start"
                  color="inherit"
                  aria-label="menu"
                  onClick={backButtonProps.onClick}
                >
                  <ChevronRightTwoTone />
                </IconButton>
              </Fade>
            )}
            <img src={LogoJpeg} className={classes.logoImage} alt="logo" />
            <Box className={classes.spacer} />
            <Typography variant="h6" className={classes.appBarTitle}>
              {title}
            </Typography>
            {buttons}
          </Box>
        </Container>
      </Toolbar>
    </MuiAppBar>
  );
}

export default AppBar;
